.FontAwesomeIcon,
.NavigationItems {
  display: flex;
  padding: 0;
}
.NavigationItems {
  margin: 0;
  list-style: none;
  align-items: center;
  height: 100%;
  flex-flow: row;
}

/* .NavigationItems > li {
  display: inline-block;
  width: 100%;
} */

.FontAwesomeIcon li svg:hover {
  color: #cbb26a;
}

@media (max-width: 801px) {
  .NavigationItems {
    margin-top: 50px;
    flex-flow: column;
  }

  .FontAwesomeIcon,
  li.Items {
    padding: 10px;
  }

  .FontAwesomeIcon {
    margin-top: 50px;
    font-size: xx-large;
  }

  .FontAwesomeIcon li {
    margin-top: 10px;
    padding-bottom: 10px;
  }
}
